
@import url('https://fonts.googleapis.com/css?family=Wallpoet|VT323|Ubuntu|Julee|Lora|Kalam|Merienda');
.App{
  text-align: center;
  background-color: #28282b;
  color: whitesmoke;
  height: 100vh;
  width: 100vw;
  font-family: Ubuntu;
  display: grid;
  grid-template-rows: 3.2cm auto;
  /* cursor: not-allowed; */

}

h1{
  margin: 0px 0px;
 
}
.photos{
  display: flex;
  column-gap: 20vw;
}
.switchM{
  cursor: pointer;
  background-color: blanchedalmond;
  border: none;
  font-weight: bold;
}

.Lower{
  overflow-y: scroll;
}
.link{
  color: whitesmoke;
}
a{
  text-decoration: none;
}
a:hover{
  text-decoration: underline;
}
.nav{
  
  padding-left: 10vw;
  padding-right: 10vw;
  position: sticky;
  
}
.repo{
  color: blueviolet;
}
.menu{
  display: flex;
  flex-wrap: wrap;
  column-gap: .5cm;
}
.logoText{
  text-align: left;
  padding-top: .8cm;
  font-family: Lora;
  color: whitesmoke;
  
}
.mode{
  padding-top: .9cm;
  width: .8cm;
  height: .8cm;
  cursor: pointer;
}
.logoSection{
  display: flex;
  justify-content: space-between;
  
}
mark{
  
  background-color: violet;
}

.aboutHeadText{
  color: blueviolet;
  font-size: 50px;
  text-decoration: underline;


}
#about{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 3vh;
  text-align: left;
  
}



.aboutTextPara{
  font-family: Ubuntu;
  font-size: 20px;
  margin: 15px 0px;
}

.aboutImg{
  width: 7cm;
  height: 6cm;
  border: none;
  margin-top: 20px;
  border-radius: 10px;

}
.aboutSvg{
  width: 7cm;
  height: 6cm;
  border: none;
  margin-top: 20px;
  border-radius: 10px;

}
#skills{
  padding-left: 10vw;
  padding-top: 5vh;
  text-align: left;
  
}
#contact{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 3vh;
  text-align: left;
}
.contactImgs{
  padding-left: 5vw;
  padding-right: 5vw;
  display: flex;
  column-gap: .3cm;
}
.contactImg{
  padding-top: 2vh;
  width: 1cm;
}
.contactSvg{
    display: flex;
    column-gap: 20vw;
}
.contactForm{
	width: 400px;
	height: 350px;
	border: none;
	
}
.contactFormSvg{
	width: 300px;
	height: 350px;
	border: none;
	
}
#projects{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 5vh;
  text-align: left;
  

}


.projectsMe{
  padding-left: 5vw;
  padding-right: 5vw;
}

.projectsMe:hover{
  transform: scale(1.13 );
  transition: .12s;
  box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
  
}

.projectName{
  color: blueviolet;
  font-size: 30px;
  font-family: Ubuntu;
  cursor: pointer;

}
.skillssss{
   padding-left: 5vw; 
   padding-right: 5vw; 
  display: flex;
  column-gap: 15px;
  flex-wrap: wrap;
  row-gap: 15px;
}

.skillName{
  color: blueviolet;
  font-size: 24px;
  font-family: Ubuntu;
  /* padding-top: 1vh; */
}

.projectDetails{
  padding-top: 1vh;
}
.projectNameHead{
  display: flex;
  column-gap: .1cm;

 
}
.direct{
  width: .4cm;
  height: .4cm;
  margin-top: 1.7vh;
  
  
}
.directKofi{
  width: .35cm;
  height: .35cm;
  margin-top: .2vh;
}
.footer{
    display: flex;
    justify-content: center;
    align-content: center;
    column-gap: .1cm;
  
}


.tagLine{
  margin: 0px 0px;
  font-size: 26px;
  
  color: blueviolet;
 
  font-family: Ubuntu;
  font-weight: bold;
}


#support{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 5vh;
  text-align: left;
 
}

.coffee{
  width: 1.5cm;
  height: 1.5cm;
 
  z-index: 1;
  top: 85%;
  left: 86%;
  position: fixed;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: blueviolet;
  box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
  cursor: pointer;

}
.scrollToTop{
  width: 1.5cm;
  height: 1.5cm;
 
  z-index: 1;
  top: 90%;
  left: 85%;
  position: fixed;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: blueviolet;
  box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
  cursor: pointer;
 

}

.coffeeImg{
  width: 1cm;
  height: 1cm;
  cursor: pointer;

  
}


#kofiframe{
  z-index: 100;
  height: 0vh;
	width: 380px;
  left: 32%;
  top: 8%;
  position: fixed;
 
  border: none;
	border-radius: 10px;
	
  transition: 0.7s;
}

.closebtn{
  z-index: 120;
  position: fixed;
  left: 52%;
  top: 1%;
  font-size: 50px;
  display: none;
  color: red;
  cursor: pointer;
}

.skill{
  width: 3cm;
  height: 4cm;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: .4cm;
  justify-content: center;
  /* border: 1px  white solid; */
  
}

.skillCircle{
  width: 2.2cm;
  height: 2.2cm;
  color: blueviolet;
}

/* css for different viewports - 1000px */
@media screen and (max-width:1000px){
	.abc{
		display: none;
	}
  .contactSvg{
    column-gap: 10vw;
  }
  
}


/* css for different viewports - 800px */
@media screen and (max-width:800px){
  .photos{
    column-gap: 10vw;
  }
  .contactFormSvg{
    display: none;
  }
  
}


/* css for different viewports - 390px */
@media screen and (max-width:390px){
  .skill{
    width:2.5cm;
    height: 3cm;
    /* row-gap: .5cm; */
  }
  .skillName{
    font-size: 20px;
  }
  .skillCircle{
    width: 2cm;
  }
  .skillssss{
    row-gap: .8cm;
  }
}


@media screen and (max-width:400px){
	#kofiframe{
		width: 300px;
	}
  
 
}

@media screen and (max-width:300px){
