@media screen and (max-width:415px){
	.contactForm{
		width: 300px;	}
    .App{
      /* grid-template-rows: 4cm auto; */
    }
   
    
}
@media screen and (max-width:400px){
  .aboutHeadText{
    font-size: 40px;
  }
  .coffee{
   
    left: 70%;
  }
  .scrollToTop{
    left: 70%;
  }
  
  #kofiframe{
		width: 280px;
