::-webkit-scrollbar {
  width: 10px;
  height: 0px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 

::-webkit-scrollbar-thumb {
  background: slateblue; 
}


::-webkit-scrollbar-thumb:hover {
  background: blueviolet  ; 
}
