.Home{
    padding-left: 10vw;
    padding-right: 10vw;
    padding-top: 3vh;
    text-align: left;
}
.aboutHome{
    color: blueviolet;
    font-size: 50px;
   text-decoration: none;
}

.latestBlogsHome{
    color: blueviolet;
    font-size: 38px;
}
h2{
    margin: 8px 0px;
    font-weight: 400;
}
h4{
    margin: 8px 0px;
}
.blogHome{
    display: flex;
    justify-content: space-between;
    padding-top: 1vh;
    align-items: center;
    margin-right: 5vw;
    border-bottom: 1px solid gray;
}
.blogHomeDate{
    color: green;
}
.favouritesHome{
    display: flex;
    column-gap: .5cm;
    row-gap: .5cm;
    flex-wrap: wrap;
}
.favourite{
    border: 1px solid gray;
    width: 8cm;
    height: 3.6cm;
    border-radius: 7px;
}
.upperFav{
    padding: 0.7vh .7vw 0.7vh .7vw;
    display: flex;
    justify-content: space-between;
    border-bottom: 0.5px solid gray;
}
.favName{
    margin: 5px;
    font-size: 23px;
    font-weight: 600;
}
.favYear{
    margin: 10px 0px;
    color: green;
    font-weight: 600;
}
.favDesc{
    margin: 10px 0px;
    padding-left: 1vw;
    font-size: 21px;
}
.favButtons{
    display: flex;
    justify-content:center ;
    column-gap: .3cm;
}
#favButtonInd{
    width: 2.2cm;
    display: flex;
    align-items: center;
    font-size: 20px;
    column-gap: .15cm;
    height: .8cm;
    border: 1px solid gray;
    border-radius: 7px; 
    padding-left: 5px;
}
.directH{
    width: .4cm;
    height: .4cm;
    /* margin-top: 1.7vh; */
    
    
  }

  @media screen and (max-width:800px){
    .favourite{
        width: 7cm;
    }
  }
  @media screen and (max-width : 700px){
    .favourite{
        margin-left: 10vw;
    }
  }

  @media screen and (max-width:600px){
    .aboutHome{
        font-size: 40px;
    }
    .tagLineHome{
        font-size: 25px;
    }
    .latestBlogsHome{
        font-size: 35px;
    }
    .blogHomeName{
        font-size: 22px;
    }
    .blogHomeDate{
        font-size: 14px;
    }
  }

  @media screen and (max-width:500px){
    .aboutHome{
        font-size: 35px;
    }
    .tagLineHome{
        font-size: 20px;
    }
    .latestBlogsHome{
        font-size: 33px;
    }
    .blogHomeName{
        font-size: 20px;
    }
    .blogHomeDate{
        font-size: 12px;
    }
  }

   @media screen and (max-width:400px){
    .favourite{
        width: 6cm;
    }
    .favDesc{
        font-size: 19px;
    }
  }
  @media screen and (max-width:350px){
    .favourite{
        width: 6cm;
         margin-left: 5vw;
    }
    .favName{
        font-size: 21px;
    }
    .favDesc{
        padding-left: 3vw;
        font-size: 17px;
    }
    #favButtonInd{
        width: 2cm;
        font-size: 17px;
    }
    .directH{
        width: .3cm;
        height: .3cm;
    }
    .aboutHome{
        font-size: 30px;
    }
    .tagLineHome{
        font-size: 17px;
    }
    .latestBlogsHome{
        font-size: 31px;
    }
    .blogHomeName{
        font-size: 17px;
    }
    .blogHomeDate{
        font-size: 10px;
    }
  }

    @media screen and (max-width:300px){
    .favourite{
        width: 6cm;
        margin-left: 0vw;
       
    }
    
  }

    @media screen and (max-width:270px){
    .favDesc{
        font-size: 15px;
    }
    
  }